export default class Model {

  // Request to Volkov API [Network]

  _url = `http://vm-kvdovin.beget:8080/index.html`;

  getRasp = async () => {
    const res = await fetch(this._url);
    if (!res.ok) {
        throw new Error (`Could not fetch ${this._url}, received ${res.status}`);
      }
      return res.json();

  }

  // Generate Calendar [View]

  generateMonth = (year, mon) => {
    // How many days in this month?
    const daysInThisMonth = (new Date(year, mon) - new Date(year, mon-1)) / 1000 / 3600 / 24;
    // Does firt day is Monday?
    const firstDateIs = new Date(year, mon-1).getDay();
    // Generate month
    const list = [];
    for (let i = 1; i <= daysInThisMonth; i++) {
      list.push(i)
    }
    // Add offset in month table ( If Monday != 1st. date )
    // Offset is just null eq. at the start or end month table
    if ((firstDateIs !== 1) && (firstDateIs !== 0)) {
      for (let i = 1; i < firstDateIs; i++) {
        list.unshift(null)
      }
    }
    // If 1st. date is Sunday
    if (firstDateIs === 0) {
      for (let i = 1; i <= 6; i++) {
        list.unshift(null)
      }
    }
    // Array must has 42 length, if arr < 42, we need add empty values at the end
    if (list.length < 42) {
      for (let i = list.length; i < 42; i++) {
        list.push(null)
      }
    }
    // If all is OK
    if (list.map === Array.prototype.map) {
      return list;
    }
  }

  generateYear = (year) => {
    // Create empty array
    let yearArray = [];
    // Add months to array
    for (let i = 1; i <= 12; i++) {
      yearArray[i] = this.generateMonth(year, i)
    }
    return yearArray;
  }

  // Get Dates array [Logic]

   getDatesArr = (start, end) => {
     // Normalize data
  	var startDate = new Date(String(start));
  	var current = startDate;
  	var endDate = new Date(String(end));
  	var arr = [];

    // Check | prevent hidden state
  	if (endDate < startDate) {
      while (current > endDate) {
        arr.push(current.toLocaleDateString('ru').substr(0, 10).replace(/\./g, '-'));
        current.setDate(current.getDate() - 1);
      }
  	}

  	while (current <= endDate) {
  	arr.push(current.toLocaleDateString('ru').substr(0, 10).replace(/\./g, '-'));
  	current.setDate(current.getDate() + 1);

  	}
  	return arr.map(this._prepareDates);
  }

  _prepareDates = (i) => {
    return `${i.substr(6)}-${i.substr(3,2)}-${i.substr(0,2)}`;
  }

  // Get [day, month] index

  _dayIndex = (date) => {
    const year = new Date(date).getFullYear();
    const month = new Date(date).getMonth();
    const day = new Date(date).getDate();
    const firstDateIs = new Date(year, month).getDay();
    let result = day;

    // Add offset in month table ( If Monday != 1st. date )
    // Offset is just null eq. at the start or end month table
    if ((firstDateIs !== 1) && (firstDateIs !== 0)) {
      for (let i = 1; i < firstDateIs; i++) {
        result++;
      }
    }
    // If 1st. date is Sunday
    if (firstDateIs === 0) {
      for (let i = 1; i <= 6; i++) {
        result++;
      }
    }
    return result - 1;
  }

  _monthIndex = (day) => {
    const res =  new Date(day).getMonth() + 1;
    if (Number.isInteger(res)) return res;
  }

  _yearIndex = (day) => {
    return new Date(day).getFullYear();
  }

  // Handlers & helpers (click, mouseover etc.)

  pushDays = () => {
    const result = {};
    const orders = this.state.api.vacators;
    // Looping API data
    orders.forEach( i => {
      const worker = i.name;
      const days = this.model.getDatesArr(i.startdate, i.enddate);

      if (!days) {
        console.error('Invalid days arr.', i.name);
        return;
      }
      // Looping every day
      for (let i = 0; i < days.length; i++) {
        const year = String(this.model._yearIndex(days[i]));
        const month = this.model._monthIndex(days[i]);
        const day = this.model._dayIndex(days[i]);

        // Create in result obj. year key with months arrays
        if (!result[year]) {
          result[year] = [];
          for (let i=1; i<=12; i++) {
            result[year][i] = [];
            for (let j = 0; j<42; j++) {
              result[year][i].push(null)
            }
          }
        }

        // Create day array
        if (!result[year][month][day]) {
          result[year][month][day] = [];
        }
        // Some wizzard
        result[year][month][day].push(worker)
      }
    });
    return result;
  }

}
