import React, {Component} from 'react';
import Month from './month';
import './calendar.css';
import Model from '../../services/Model';
import Loader from '../loader';
import Modal from '../modal';
import ButtonBar from '../button-bar';

export default class Calendar extends Component {
  constructor(props) {
    super(props);
    this.state = { view: this.model.generateYear(this.year || 2019),
                   api: null,
                   dates: {},
                   ready: false,
                   year: new Date().getFullYear(),
                   picker_mode: false,
                   picker_date_1: null,
                   picker_date_2: null,
                   checked: null,
                   modal: false,
                 };

     if (!this.state.checked) {
       const empty = {};
       empty[this.state.year] = [];
       for (let i=1; i<=12; i++) {
         empty[this.state.year][i] = [];
         for (let j = 0; j<42; j++) {
           empty[this.state.year][i].push(null)
         }
       }
       this.state.checked = empty;
     }
  }

  // All Date, Calendar logic (Dates methods) & Network API methods
  model = new Model();


  // Lifecicle hooks

  componentDidMount() {
    this.model.getRasp()
      .then( res => {
        this.setState( { api: res } );
      });
  }

  componentDidUpdate() {
    if (!this.state.ready) {
      this.setState( state => {
        return ({
          dates: this.pushDays(),
          ready: true
        })
      });
//      console.log(this.state);
    }
  }

  // Generate Array with busy days
  pushDays() {
    const result = {};
    const orders = this.state.api.vacators;

    // Looping API data
    orders.forEach( i => {
      const worker = i.name;
      const days = this.model.getDatesArr(i.startdate, i.enddate);

      if (!days) {
        console.error('Invalid days arr.', i.name);
        return;
      }
      // Looping every day
      for (let i = 0; i < days.length; i++) {
        const year = String(this.model._yearIndex(days[i]));
        const month = this.model._monthIndex(days[i]);
        const day = this.model._dayIndex(days[i]);

        // Create in result obj. year key with months arrays
        if (!result[year]) {
          result[year] = [];
          for (let i=1; i<=12; i++) {
            result[year][i] = [];
            for (let j = 0; j<42; j++) {
              result[year][i].push(null)
            }
          }
        }

        // Create day array
        if (!result[year][month][day]) {
          result[year][month][day] = [];
        }
        // Some wizzard
        result[year][month][day].push(worker)
      }
    });
    return result;
  }

  // Get date from DOM | Normalyze data to `new Date` format
  pickDate = (e) => {
    // Click on table cell only !!!
    if (!e.target.className.match(/td_/i)) {
      return;
    }

    // Extract values via DOM
    if (e.target.children[0]) {
      var date = e.target.children[0].textContent;
    } else {
      var date = e.target.textContent;
    }

    var month = e.target.parentNode.parentNode
                 .parentNode.parentNode.parentNode
                 .children[0].textContent;

    // This array helps convert text value to number format
    var monthsArr = {
      "Январь": "01",
      "Февраль": "02",
      "Март": "03",
      "Апрель": "04",
      "Май": "05",
      "Июнь": "06",
      "Июль": "07",
      "Август": "08",
      "Сентябрь": "09",
      "Октябрь": "10",
      "Ноябрь": "11",
      "Декабрь": "12"
    };
    console.log(date);
    return (`${this.state.year}-${monthsArr[month]}-${date}`);

  }

  // Get range dates
  pickRange = (e) => {
    // if is it not first choise
    if (this.state.picker_date_1 && this.state.picker_date_2) {
    // TODO: make this checking
    //  this.state.picker_date_1 = null;
    //  this.state.picker_date_2 = null;
    }

    // if click was on right cell
    if (e.target.className.match(/td_/i)) {

      const firstDate = this.pickDate(e);
      if (!this.state.picker_mode) {
        this.setState( state => {
          return {
                   picker_mode: true,
                   picker_date_1: firstDate
                 };
        });
      }

      if (this.state.picker_mode) {
        const secondDate = this.pickDate(e);
        this.setState( state => {
          return {
                  picker_date_2: secondDate,
                  picker_mode: false,
                 };
        })
      }

    }
  }

  // Needs to animate pick up range
  mouseOverHandler = (e) => {
    // If Picker Mode is on
    if (this.state.picker_mode) {
      // If mouse cursor over on day's table cell
      if (e.target.className.match(/td_/i)) {
        const dates = this.model.getDatesArr(this.state.picker_date_1, this.pickDate(e));

        // [DEBUG] Mouse over
        //console.log('Date', this.pickDate(e));

        const result = {};
        if (dates === undefined) {
          console.log("date 1 is", this.state.picker_date_1);
          console.log("date 2 is", this.pickDate(e));

        }

        for (let i = 0; i < dates.length; i++) {
          const year = String(this.model._yearIndex(dates[i]));
          const month = this.model._monthIndex(dates[i]);
          const day = this.model._dayIndex(dates[i]);

          // Create in result obj. year key with months arrays
          if (!result[year]) {
            result[year] = [];
            for (let i=1; i<=12; i++) {
              result[year][i] = [];
              for (let j = 0; j<42; j++) {
                result[year][i].push(null)
              }
            }
          }

          // Create day array
          if (!result[year][month][day]) {
            result[year][month][day] = [];
          }
          // Some wizzard
          result[year][month][day].push('checked')
        }

        this.setState(state => {
          return {
            checked: result
          };
        })
      }
    }
  }

  // Open popup to confirm your picked dates
  toggleModalWindow = () => {
    let open = this.state.modal;
    // const overlay = document.querySelector('.modal')
    // const modalWindow = document.querySelector('.modal-content')
    //
    if (!open) {
    //   overlay.classList.remove('fade')
    //   modalWindow.classList.add('show')
    //   overlay.style.display = 'block'
    //   overlay.style.backgroundColor = 'rgba(0, 0, 0, .7)'
    //   document.body.classList.add('modal-open')
      this.setState( state => {
        return { modal: true };
      })
    } else {
    //   modalWindow.classList.remove('show')
    //   overlay.classList.add('fade')
    //   overlay.style.display = 'none'
    //   document.body.classList.remove('modal-open')
      this.setState( state => {
        return { modal: false };
      })
    }
  }

  render() {
    const {view, year} = this.state;
    if (!this.state.ready) {
      return ( <Loader /> );
    } else {

    return (
      <div className="calendar container" onClick={this.pickRange} onMouseOver={this.mouseOverHandler}>

        <ButtonBar toggleModalWindow={this.toggleModalWindow}/>

        <Month year={year} month={view[1]} name="1" dates={this.state.dates[year][1]} checked={this.state.checked[year][1]} />
        <Month year={year} month={view[2]} name="2" dates={this.state.dates[year][2]} checked={this.state.checked[year][2]} />
        <Month year={year} month={view[3]} name="3" dates={this.state.dates[year][3]} checked={this.state.checked[year][3]} />
        <Month year={year} month={view[4]} name="4" dates={this.state.dates[year][4]} checked={this.state.checked[year][4]} />
        <Month year={year} month={view[5]} name="5" dates={this.state.dates[year][5]} checked={this.state.checked[year][5]} />
        <Month year={year} month={view[6]} name="6" dates={this.state.dates[year][6]} checked={this.state.checked[year][6]} />
        <Month year={year} month={view[7]} name="7" dates={this.state.dates[year][7]} checked={this.state.checked[year][7]} />
        <Month year={year} month={view[8]} name="8" dates={this.state.dates[year][8]} checked={this.state.checked[year][8]} />
        <Month year={year} month={view[9]} name="9" dates={this.state.dates[year][9]} checked={this.state.checked[year][9]} />
        <Month year={year} month={view[10]} name="10" dates={this.state.dates[year][10]} checked={this.state.checked[year][10]} />
        <Month year={year} month={view[11]} name="11" dates={this.state.dates[year][11]} checked={this.state.checked[year][11]} />
        <Month year={year} month={view[12]} name="12" dates={this.state.dates[year][12]} checked={this.state.checked[year][12]} />

        <Modal startDate={this.state.picker_date_1}
               endDate={this.state.picker_date_2}
               toggleModalWindow={this.toggleModalWindow}
               modal={this.state.modal}
               />

      </div>
    );
  }
  }
}
