import React, {Component} from 'react';
import './modal.css';

class Modal extends Component {
  render() {


    const { startDate, endDate, toggleModalWindow, modal } = this.props;
    let styles, classes, classesWindow;

    if (modal) {
      styles = { display: "block", backgroundColor: "rgba(0,0,0,.7)" };
      classes = "modal";
      classesWindow = "modal-dialog show"
    } else {
      styles = { /* display: "none" */ };
      classes = "modal hide";
      classesWindow = "modal-dialog"
    }
      return (
        <div className={classes} style={styles} onClick={toggleModalWindow} >
          <div className={classesWindow} role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h4 className="modal-title">Оставить заявку</h4>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>

              <div className="modal-body">
                <p> Ты уверен(a), что хочешь взять отпуск в период: <br/>
                  с <span className="modal__date"> { new Date(startDate).toLocaleDateString('ru') } </span>
                  по <span className="modal__date"> { new Date(endDate).toLocaleDateString('ru') } </span>
                </p>
              </div>

              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Отмена</button>
                <button type="button" className="btn btn-primary">Отправить</button>
              </div>
            </div>
          </div>
        </div>
      )
  }

}

export default Modal;
