import React, {Component} from 'react';

export default class Day extends Component {
  render() {
    const {day, workers, checked } = this.props;
    // console.log(`In day.js this.props.day is ${day}`);
    if (checked) {
      return (
        <td>
          <div className="td_checked">{day}</div>
        </td>
      );
    }

    if (!day) {
      return (
        <td>
          <div className="td_empty">{day}</div>
        </td>
      )
    }

    if (workers) {
      const text = workers.join(', ')
      return (
        <td>
            <div className="td_busy">
             <span>{day}</span>
             <span className="tooltip-day">{text}</span>
            </div>

        </td>
      );
    }

    if (day) {
      return (
          <td>
            <div className="td_content">{day}</div>
          </td>
        );
    }
  }
}
